import { Component, Event, EventEmitter, Listen, Prop, State, h } from '@stencil/core';

@Component({
  tag: 'mdx-textarea',
  styleUrl: 'textarea.scss',
  shadow: true,
})
export class Textarea {
  @Event()
  inputChange: EventEmitter<{ input: string }>;

  @Prop()
  disabled = false;

  @Prop()
  required = false;

  @Prop()
  label!: string;

  @Prop()
  value = '';

  @Prop()
  placeholder = '';

  @Prop()
  hint?: string;

  @Prop()
  error?: string;

  @Prop()
  counter?: number;

  @State()
  focused = false;

  @State()
  keyboardEvent = false;

  @Listen('click', { capture: true, target: 'window' })
  handleClick() {
    this.keyboardEvent = false;
  }

  @Listen('keydown', { capture: true, target: 'window' })
  handleKeyDown() {
    this.keyboardEvent = true;
  }

  private handleValue(e: Event) {
    const v = (e.currentTarget as HTMLTextAreaElement).value;
    if (this.counter && (v.length || 0) > this.counter) {
      this.value = v.slice(0, Math.min(v.length, this.counter));
      (e.currentTarget as HTMLTextAreaElement).value = this.value;
    } else {
      this.value = v;
    }
    this.inputChange.emit({ input: this.value });
  }

  render() {
    return (
      <label class={`textarea ${this.focused && 'textarea--focused'}`}>
        <div class="textarea__label-text">
          {this.label} {this.required && <span class="asterisk">*</span>}
        </div>
        <textarea
          class="textarea__field"
          onFocus={() => this.keyboardEvent && (this.focused = true)}
          onBlur={() => (this.focused = false)}
          onInput={(e) => {
            this.handleValue(e);
            const t = e.currentTarget as HTMLTextAreaElement;
            t.style.height = '0';
            const scrollHeight = t.scrollHeight;
            t.style.height = `clamp(var(--mdx-comp-textarea-sizing-vertical-box-min),
              ${scrollHeight}px, var(--mdx-comp-textarea-sizing-vertical-box-max))`;
          }}
          required={this.required}
          placeholder={this.placeholder}
          disabled={this.disabled}
          value={this.value}
        />
        <div class="textarea__information-wrapper">
          <div>
            {this.hint && (typeof this.error === 'undefined' || this.error === '') && (
              <span class="textarea__hint">{this.hint}</span>
            )}
            {this.error && <mdx-error-hint class="textarea__error-message" text={this.error} />}
          </div>
          {this.counter && this.counter > 0 && (
            <span class="textarea__counter">
              {this.value.length} / {this.counter}
            </span>
          )}
        </div>
      </label>
    );
  }
}
