:host .textarea {
  box-sizing: border-box;
  color: var(--mdx-comp-textarea-label-color-default);
  display: flex;
  flex-direction: column;
  margin-bottom: var(--mdx-comp-textarea-gap-content-below);

  &--focused {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
    color: var(--mdx-comp-textarea-label-color-focus);
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
    outline-offset: 2px;
  }

  &__label-text {
    font: var(--mdx-comp-textarea-font-label);
    color: var(--mdx-comp-textarea-label-color-default);
  }

  &__field {
    background-color: var(--mdx-comp-textarea-background-color-default);
    border-radius: var(--mdx-comp-textarea-border-radius-default);
    border-width: var(--mdx-comp-textarea-border-width-default);
    border-style: solid;
    box-shadow: var(--mdx-comp-textarea-box-shadow-default);
    font: var(--mdx-comp-textarea-font-default);
    margin: var(--mdx-comp-textarea-gap-label-textarea) 0 var(--mdx-comp-textarea-gap-content-below) 0;
    padding: var(--mdx-comp-textarea-padding-vertical-default) var(--mdx-comp-textarea-padding-horizontal-default);
    height: var(--mdx-comp-textarea-sizing-vertical-box-min);
    resize: none;
    overflow: scroll;
    box-sizing: border-box;
    outline: none;

    &::placeholder {
      color: var(--mdx-comp-textarea-placeholder-color-default);
    }

    &:focus-within {
      background-color: var(--mdx-comp-textarea-background-color-focus);
      border-color: var(--mdx-comp-textarea-border-color-focus);
      box-shadow: var(--mdx-comp-textarea-box-shadow-focus);
      color: var(--mdx-comp-textarea-input-color-focus);
    }

    &:focus-within::placeholder {
      color: var(--mdx-comp-textarea-placeholder-color-focus);
    }
  }

  &__information-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font: var(--mdx-comp-textarea-font-supporting);
  }

  &__hint {
    color: var(--mdx-comp-textarea-hint-counter-color-default);
  }

  &:focus-within &__information-wrapper &__hint {
    color: var(--mdx-comp-textarea-hint-counter-color-focus);
  }

  &--focused {
    .textarea__field {
      background-color: var(--mdx-comp-textarea-background-color-focus);
      border-color: var(--mdx-comp-textarea-border-color-focus);
      color: var(--mdx-comp-textarea-input-color-focus);
    }

    .asterisk {
      color: var(--mdx-comp-textarea-asterisk-color-focus);
    }

    .textarea__label-text {
      color: var(--mdx-comp-textarea-label-color-focus);
    }
  }
}

:host([required]) .asterisk {
  color: var(--mdx-comp-textarea-asterisk-color-default);
  margin-left: var(--mdx-comp-textarea-gap-label-asterisk);
}

:host([disabled]) .asterisk {
  color: var(--mdx-comp-textarea-asterisk-color-disabled);
}

:host([disabled]) .textarea {
  &__label-text {
    color: var(--mdx-comp-textarea-label-color-disabled);
  }

  &__field {
    background-color: var(--mdx-comp-textarea-background-color-disabled);
    color: var(--mdx-comp-textarea-input-color-disabled);
    box-shadow: var(--mdx-comp-textarea-box-shadow-disabled);
  }

  &__field::placeholder {
    color: var(--mdx-comp-textarea-placeholder-color-disabled);
    background-color: var(--mdx-comp-textarea-background-color-disabled);
  }

  &__hint,
  &__error-message {
    color: var(--mdx-comp-textarea-hint-counter-color-disabled);
  }
}

:host([error]) .textarea {
  &__field {
    background-color: var(--mdx-comp-textarea-background-color-error);
    border-color: var(--mdx-comp-textarea-border-color-error);
    box-shadow: var(--mdx-comp-textarea-box-shadow-error);
    color: var(--mdx-comp-textarea-input-color-error);

    &::placeholder {
      color: var(--mdx-comp-textarea-placeholder-color-error);
    }
  }

  &__label-text {
    color: var(--mdx-comp-textarea-label-color-error);
  }
}
